:root {
    /*color*/
    --body-bg-color: #ffffff;
    --sidebar-bg-color: #f4f4f4;
    --text-color: #333333;
    --title-color: #111111;
    --date-color: #1f94c2;
  
    /*dark-body*/
    --icon-color: var(--text-color);
    --circle-color: #aaa;
    --circle-color-plain: #555;
  
    /*fonts*/
    --body-font: "Poppins", sans-serif;
    --h1-font-size: 1.7rem;
    --h2-font-size: 1.3rem;
    --h3-font-size: 1.1rem;
    --h4-font-size: 1rem;
  
    /*helpers*/
    --spacing-1: 0.4rem;
    --spacing-2: 0.8rem;
    --spacing-3: 1.2rem;
    --spacing-4: 1.5rem;
    --spacing-5: 2rem;
  }

ul{
    padding-left: .1rem;
  }
  li{
    margin-bottom: var(--spacing-2);
  }

  .skills{
    text-align: left;
  }
  .icon{
      display: flex;
  }
  .icon svg{
      margin-right: 1rem;
  }