.user{
    text-align: center;
}
.user_avatar{
    height: 10rem;
    border-radius:  50%;
    -webkit-border-radius:  50%;
    -moz-border-radius:   50%;
    -ms-border-radius:  50%;
    -o-border-radius:   50%;
}

.user_infos{
    text-align: left;
    padding: var(--spacing-3) 0 var(--spacing-1) 0;
}

.user_info{
    display: flex;
}

.user_info svg{
    margin-right: 1rem;
}