@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,300;0,400;1,100;1,300;1,400);

*,
::before,
::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
}

:root {
  /*color*/
  --body-bg-color: #ffffff;
  --sidebar-bg-color: #f4f4f4;
  --text-color: #333333;
  --title-color: #111111;
  --date-color: #1f94c2;
  /*dark-body*/
  --dark-body-bg-color: #242424;
  --dark-sidebar-bg-color: #171717;
  --dark-text-color: #d8d8d8;
  --dark-title-color: #ffffff;
  --dark-date-color: #e6ef71;

  /*dark-body*/
  --icon-color: var(--text-color);
  --circle-color: #aaa;
  --circle-color-plain: #555;
  /*dark-body*/
  --dark-icon-color: var(--dark-text-color);
  --dark-circle-color: rgb(108, 108, 108);
  --dark-circle-color-plain: rgb(238, 238, 238);

  /*fonts*/
  --body-font: "Poppins", sans-serif;
  --h1-font-size: 1.7rem;
  --h2-font-size: 1.3rem;
  --h3-font-size: 1.1rem;
  --h4-font-size: 1rem;

  /*helpers*/
  --spacing-1: 0.4rem;
  --spacing-2: 0.8rem;
  --spacing-3: 1.2rem;
  --spacing-4: 1.5rem;
  --spacing-5: 2rem;
}

body {
  font-family: "Poppins", sans-serif;
  font-family: var(--body-font);
  font-weight: 200;
  color: #333333;
  color: var(--text-color);
  background: #ffffff;
  background: var(--body-bg-color);
}
.dark {
  color: #d8d8d8;
  color: var(--dark-text-color);
  background: #242424;
  background: var(--dark-body-bg-color);
}

h1,
h2,
h3 {
  margin-bottom: 0.4rem;
  margin-bottom: var(--spacing-1);
  font-weight: 400;
  color: #111111;
  color: var(--title-color);
}

dark.h1,
dark.h2,
dark.h3 {
  color: #ffffff;
  color: var(--dark-title-color);
}

h1 {
  font-size: 1.7rem;
  font-size: var(--h1-font-size);
  text-transform: uppercase;
}

h2 {
  display: flex;
  align-items: center;
  margin-bottom: 1.5rem;
  margin-bottom: var(--spacing-4);
  font-size: 1.3rem;
  font-size: var(--h2-font-size);
  text-transform: uppercase;
  letter-spacing: 4px;
}
.h2-profil{
  text-align: center;
  font-weight: 500;
}
h3 {
  font-size: 1.1rem;
  font-size: var(--h3-font-size);
}

a {
  text-decoration: none;
  color: #333333;
  color: var(--text-color);
}
.dark a {
  color: #d8d8d8;
  color: var(--dark-text-color);
}

button {
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.dark button {
  color: #d8d8d8;
  color: var(--dark-text-color);
}

p {
  margin-bottom: 0.8rem;
  margin-bottom: var(--spacing-2);
}

/*mb ici pour faire des ajustements */
.mb3 {
  margin-bottom: 1.2rem;
  margin-bottom: var(--spacing-3);
}
.mb5 {
  margin-bottom: 2rem;
  margin-bottom: var(--spacing-5);
}
.grid_row {
  margin: 1.2rem;
  margin: var(--spacing-3);
}
/*grid*/
@media screen and (min-width: 768px) {
  .grid_container {
    display: grid;
    grid-template-columns: 1fr 3fr;
  }
  .grid_row {
    display: grid;
    grid-template-columns: 1fr 2fr;
    margin: 0;
  }
}

/*layouts*/
.sidebar {
  padding: 1.5rem;
  background: #f4f4f4;
  background: var(--sidebar-bg-color);
}

.dark .sidebar {
  background: #171717;
  background: var(--dark-sidebar-bg-color);
}

.actions {
  display: flex;
  justify-content: space-between;
}

/*icon rond*/
.circle {
  display: inline-block;
  width: 20px;
  height: 20px;
  margin-right: 8px;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  background: #aaa;
  background: var(--circle-color);
}
.circle-plain {
  background: #555;
  background: var(--circle-color-plain);
}
.dark .circle {
  background: rgb(108, 108, 108);
  background: var(--dark-circle-color);
}
.dark .circle-plain {
  background: rgb(238, 238, 238);
  background: var(--dark-circle-color-plain);
}
.main {
  padding: 1.5rem;
}

/*generate pdf*/
#cv-print {
  width: 90% !important;
  
}
.cv-print .actions {
  display: none;
}
.cv-print ul {
  margin: 0;
  padding: 0;
}
.cv-print ul li {
  list-style-type: none;
}
.cv-print ul li::before {
  content: ".";
  display: inline-block;
  font-size: 110%;
  padding-right: 10px;
}
.cv-print .grid_container {
  font-size: 12px;
  max-width: 210mm;
}
.cv-print .sidebar {
  height: 297mm;
}
.cv-print .user_info svg {
  display: none !important;
}
.cv-print .circle {
  width: 10px;
  height: 10px;
}
.cv-print .grid_date {
  margin-top: 5px;
}

.user{
    text-align: center;
}
.user_avatar{
    height: 10rem;
    border-radius:  50%;
    -webkit-border-radius:  50%;
    -moz-border-radius:   50%;
    -ms-border-radius:  50%;
    -o-border-radius:   50%;
}

.user_infos{
    text-align: left;
    padding: var(--spacing-3) 0 var(--spacing-1) 0;
}

.user_info{
    display: flex;
}

.user_info svg{
    margin-right: 1rem;
}
.skills{
    margin: var(--spacing-3) 0;
}

.skill{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

